.bannerSection {
  width: 100%;
  height: 105dvh;
  background-size: cover;
  display: flex;
  justify-content: right;
  align-items: center;
}

@media only screen and (max-width: 1000px) {
  .bannerSection {
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
}
